.login{
    background-color: white;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items:center ;
}
.login__container{
    padding: 100px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 1px 3px rgb(12, 11, 11), 0 1px;
}
.login__container >img{
    object-fit: contain;
    height: 100px;
    margin-bottom: 40px;

}
.login__container >button{
    margin-top: 50px;
    text-transform: inherit !important;
    background-color: green !important;
    color: white;
}